import React from 'react'
import rupeeSign from './assets/images/rupeeIcon.png'

const RupeeSignComponent = () => {
  return (
    <>
      <img src={rupeeSign} style={{
        height: '25px',
        width: '25px',
        opacity: '0.5',
        marginRight: '18px',
      }}  alt='indian Rupee'/>
    </>
  )
}

export default RupeeSignComponent
